import React from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import Helmet from 'react-helmet'


// import Lightbox from 'react-images'
import Gallery from "../components/Gallery"

import thumb01 from '../assets/images/thumbs_ML/bolso_materob.jpg'
import thumb02 from '../assets/images/thumbs_ML/bolso_matero-2.jpg'
import thumb03 from '../assets/images/thumbs_ML/bolso_matero-3.jpg'

import full01 from '../assets/images/fulls/bolso_matero.jpg'
import full02 from '../assets/images/fulls/bolso_matero-2.jpg'
import full03 from '../assets/images/fulls/bolso_matero-3.jpg'

const DEFAULT_IMAGES = [
    { id: '1', src: full01, thumbnail: thumb01, caption: 'Photo 1', description: 'Set De Jardín De Infantes Personalizado En Quilmes Cole Kit'},
    { id: '2', src: full02, thumbnail: thumb02, caption: 'Photo 2', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '3', src: full03, thumbnail: thumb03, caption: 'Photo 3', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'}
];

class HomeIndex extends React.Component {

    

    render() {
        //const siteTitle = this.props.data.site.siteMetadata.title
        //const siteDescription = this.props.data.site.siteMetadata.description

        return (
            <div>
                <Helmet>
                        <title>hi</title>
                        <meta name="description" />
                </Helmet>

                <div id="main">

                    <section id="one">
                        <header>
                            <h2>Bolso Matero</h2>
                            <h4>$190</h4> 
                        </header>
                        <p>DESCRIPCIÓN:<br />
                        Bolso Matero Sublimado. <br />
                        Se pueden personalizar con cualquier imagen o foto.</p>


                         
                        <a mp-mode="dftl" href="https://www.mercadopago.com/mla/checkout/start?pref_id=8068205-00a944d3-6bd7-48bf-bddb-187d8c0fc47d" name="MP-payButton" className='blue-ar-l-rn-none preview__button'>Pagar</a>
                        <br /><br />  
                        
                        
                       
                        <Link to="/">Volver</Link>

                        <br /><br />    
                    </section>
            
                    
                        <Gallery images={DEFAULT_IMAGES.map(({ id, src, thumbnail, caption, description }) => ({
                            src,
                            thumbnail
                        }))} />

                        
                    

                </div>

            </div>
        )
    }
}

export default HomeIndex


